import React from 'react'
import "../sidenav/Sidenav.scss";
import 'boxicons';
import {
    Link, useRouteMatch
  } from "react-router-dom";
  
function Sidenav() {
    let { path, url } = useRouteMatch();
    return (
        <div>
            <div className="side-nav">
                <ul className="nav flex-column ">
                    <li className="nav-item">
                        <Link className="nav-link" to={`${url}/alerts`}>Alerts</Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" to={`${url}/modals`}>Modals</Link>
                    </li>
                </ul>

            </div>
            {/* <div className="menu-container">
                <a className="nav-link active menu" ><box-icon name='menu'></box-icon></a>
            </div> */}

        </div>
    )
}

export default Sidenav
